#loginIntro {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('/intro_background.jpg');
  background-size: cover;
}

#loginForm {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: white;
  box-shadow: 0px 0px 5px 0.2rem rgba(0,0,0,0.2);
  min-width: 300px;
  flex: 1;
  height: 100%;

}

.login-form {
  margin-top: 10vh;
  text-align: center;
}

.form-field {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px;
  font-size: 20px;
}

.input-text, .input-password {
  border: 1px solid gainsboro;
  border-radius: 5px;
  font-size: 15px;
  padding: 15px 20px;
  margin-top: 20px;
  width: 100%;
}

.input-text:hover, .input-password:hover {
  border-color: #4285F4;
}

input:focus {
  outline: none;
  border: 3px solid #4285F4;
  padding: 13px 18px 13px 18px;
}

.btn-primary{
  width: 100%;
  padding: 10px 20px;
  margin: 20px 0px 0px 0px;
  border: 1px solid #4285F4;
  border-radius: 5px;
  background: white;
  font-size: 20px;
  font-weight: 600;
  color: #4285F4;
  transition: all 0.2s ease;
}

.btn-primary:hover {
  cursor: pointer;
  background: #4285F4;
  color: white;
}

.btn-primary:focus {
  outline: none;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.notice {
  margin-top: 5px;
  font-size: 12px;
}

a {
  text-decoration: none;
  color: #4285F4;
  cursor: pointer;
}

.copyright-login-form {
  position: absolute;
  bottom: 20px;
  font-size: 14px;
}
@media (max-width: 1000px) {
  #loginIntro {
    width: 0%;
  }
}