@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic&display=swap);
:lang(en) {font-family: 'Roboto', sans-serif;}
:lang(ko) {font-family: 'Nanum Gothic', sans-serif;}
body {
  box-sizing: border-box;
  color: #444444;
  margin: 0;
  width: 100vw;
  height: 100vh;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 100vw;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.intro {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;

}

.intro-img-wrap {
  display: flex;
  align-items: center;
  justify-content: center;

}

.intro-img {
  -webkit-animation: rotate-img;
          animation: rotate-img;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.ment-wrap {
  color: white;
  margin-top: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  -webkit-animation-name: cursor;
          animation-name: cursor;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

@-webkit-keyframes rotate-img {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate-img {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes cursor {
  from {
    border-right: 3px solid rgba(200,200,200, 0.0);
  }
  to {
    border-right: 3px solid rgba(200,200,200, 1);
  }
}

@keyframes cursor {
  from {
    border-right: 3px solid rgba(200,200,200, 0.0);
  }
  to {
    border-right: 3px solid rgba(200,200,200, 1);
  }
}
#loginIntro {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5)) ), url('/intro_background.jpg');
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('/intro_background.jpg');
  background-size: cover;
}

#loginForm {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: white;
  box-shadow: 0px 0px 5px 0.2rem rgba(0,0,0,0.2);
  min-width: 300px;
  flex: 1 1;
  height: 100%;

}

.login-form {
  margin-top: 10vh;
  text-align: center;
}

.form-field {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px;
  font-size: 20px;
}

.input-text, .input-password {
  border: 1px solid gainsboro;
  border-radius: 5px;
  font-size: 15px;
  padding: 15px 20px;
  margin-top: 20px;
  width: 100%;
}

.input-text:hover, .input-password:hover {
  border-color: #4285F4;
}

input:focus {
  outline: none;
  border: 3px solid #4285F4;
  padding: 13px 18px 13px 18px;
}

.btn-primary{
  width: 100%;
  padding: 10px 20px;
  margin: 20px 0px 0px 0px;
  border: 1px solid #4285F4;
  border-radius: 5px;
  background: white;
  font-size: 20px;
  font-weight: 600;
  color: #4285F4;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.btn-primary:hover {
  cursor: pointer;
  background: #4285F4;
  color: white;
}

.btn-primary:focus {
  outline: none;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.notice {
  margin-top: 5px;
  font-size: 12px;
}

a {
  text-decoration: none;
  color: #4285F4;
  cursor: pointer;
}

.copyright-login-form {
  position: absolute;
  bottom: 20px;
  font-size: 14px;
}
@media (max-width: 1000px) {
  #loginIntro {
    width: 0%;
  }
}
